import React, { useRef, useState } from "react"; // ✅ useState 추가
import { Col, Row } from 'react-bootstrap';
import { useSelector } from "react-redux";

import { Mousewheel, Pagination } from 'swiper/modules';
import { Swiper, SwiperSlide } from 'swiper/react';

const PortDetail = () => {
  const swiperRef = useRef(null);
  const doclist = useSelector((state) => state.doclist);

  const [activeIndex, setActiveIndex] = useState(0); // ✅ 현재 슬라이드 인덱스 추적

  const handleNavClick = (index) => {
    if (swiperRef.current) {
      swiperRef.current.slideTo(index);
    }
  };

  return (
    <>
      <div className="port_nav">
        <ul>
          {doclist.map((item, i) => (
            <li key={i} onClick={() => handleNavClick(i)} style={{ cursor: 'pointer' }}>
              {item.nav}
            </li>
          ))}
        </ul>
      </div>

      <div className="port_wrap">
        <Swiper
          onSwiper={(swiper) => { swiperRef.current = swiper }}
          onSlideChange={(swiper) => setActiveIndex(swiper.activeIndex)} // ✅ 슬라이드 변경 시 인덱스 저장
          direction="vertical"
          pagination={{ clickable: true }}
          nested={true}
          modules={[Pagination, Mousewheel]}
          mousewheel={{ forceToAxis: true, releaseOnEdges: true }}
          className="mySwiper"
        >
          {doclist.map((item, i) => (
            <SwiperSlide key={i}>
              {/* ✅ 슬라이드 진입 시에만 active 클래스 부여 */}
              <div className={`port_detail_scroll_wrap ${activeIndex === i ? 'active' : ''}`}>
                {/* ✅ 슬라이드 진입 시에만 콘텐츠 렌더링 (선택) */}
                {activeIndex === i && (
                  <div
                    className="port_detail"
                    style={{ backgroundColor: item.backColor }}
                  >
                    <Row>
                      <Col md={6} lg={7}>
                        <div className="hommepage_wrap">
                          <div className="homepage_inner">
                            <img
                              src={process.env.PUBLIC_URL + item.homeImg}
                              alt={`${item.title} homepage`}
                            />
                          </div>
                        </div>
                      </Col>
                      <Col md={6} lg={5}>
                        <div className="port_text">
                          <h1>{item.title}</h1>
                          <p>{item.mTitle}</p>
                          <h5>{item.date}</h5>
                          <span className="tool">{item.tool}</span>
                          <p>{item.account}</p>
                          <div className="text_btn">
                            <span onClick={() => window.open(item.homeUrl, '_blank')}>
                              HOME PAGE
                            </span>
                            <span onClick={() => window.open(item.docUrl, '_blank')}>
                              DOCUMENT
                            </span>
                          </div>
                        </div>
                      </Col>
                    </Row>
                  </div>
                )}
              </div>
            </SwiperSlide>
          ))}
        </Swiper>
      </div>
    </>
  );
};

export default PortDetail;
