import React, { useState } from "react";

const slides = [
    {
        id: "slide-1",
        left: "/img/bg.jpg",
        right: "/img/17.jpeg",
        btn: 'Profile',
        title: 'Profilo',
        leftTextTitle: "PROFILE",
        leftTextDesc: " ",
        rightContent: (
            <div className="custom-right-content">
                <h3>방혜림</h3>
                <p>1989.08</p>
                <p>경기도 화성시</p>
                <p>illust_gyubin@naver.com</p>
                <h4>학력</h4>
                <p>신인여자고등학교 - 디자인과</p>
                <h4>자격증</h4>
                <p>ITQ Master</p>
                <p>웹디자인개발(필기)</p>
                <h4>교육이수</h4>
                <p>현장 수요에 맞는 디지털디자인(웹디자인,웹퍼블리셔)전문가 양성 - mbc아카데미</p>
            </div>
        ),
    },
    {
        id: "slide-2",
        left: "/img/bg.jpg",
        right: "/img/13.jpg",
        btn: 'SKILL',
        leftTextTitle: "SKILL",
        leftTextDesc: "전반적인 숙련도는 중정도이며, 앞으로 AI 활용 및 다양한 언어와 툴을 공부해 나가겠습니다.",
        rightContent: (
            <div className="custom2-right-content">
                <h3>SKILL</h3>
                <ul>
                    <li><img src={process.env.PUBLIC_URL +"/img/html_icon.png"} alt="icon" />HTML</li>
                    <li><img src={process.env.PUBLIC_URL +"/img/css_icon.png"} alt="icon" />CSS</li>
                    <li><img src={process.env.PUBLIC_URL +"/img/scss_icon.png"} alt="icon" />SCSS</li>
                    <li><img src={process.env.PUBLIC_URL +"/img/java_icon.png"} alt="icon" />JS</li>
                    <li><img src={process.env.PUBLIC_URL +"/img/jq_icon.png"} alt="icon" />JQ</li>
                    <li><img src={process.env.PUBLIC_URL +"/img/react_icon.png"} alt="icon" />REACT</li>
                    <li><img src={process.env.PUBLIC_URL +"/img/redux_icon.png"} alt="icon" />REDUX</li>
                </ul>
                <h3>PROGRAM</h3>
                     <ul>
                    <li><img src={process.env.PUBLIC_URL +"/img/photo_icon.png"} alt="icon" />PS</li>
                    <li><img src={process.env.PUBLIC_URL +"/img/illust_icon.png"} alt="icon" />ILLUST</li>
                    <li><img src={process.env.PUBLIC_URL +"/img/figma_icon.png"} alt="icon" />FIGMA</li>
                </ul>
            </div>
        ),
    },
];

export default function ProSlider() {
    const [activeIndex, setActiveIndex] = useState(0);

    return (
        <div className="header__main">
            <div className="slider">
                <svg
                    className="slider__mask"
                    width="0"
                    height="0"
                    viewBox="0 0 1920 1080"
                    xmlns="http://www.w3.org/2000/svg"
                    xmlnsXlink="http://www.w3.org/1999/xlink">

                    <defs>
                        {slides.map(({ id, left, right }, i) => (
                            <React.Fragment key={id}>
                                <pattern
                                    id={`pattern${i + 1}l`}
                                    patternUnits="userSpaceOnUse"
                                    width="562"
                                    height="366"
                                    viewBox="0 0 562 366"
                                >
                                    <image xlinkHref={process.env.PUBLIC_URL +left} width="600" height="600" opacity="0.7" />
                                </pattern>

                                <pattern
                                    id={`pattern${i + 1}r`}
                                    x="365"
                                    patternUnits="userSpaceOnUse"
                                    width="562"
                                    height="366"
                                    viewBox="0 0 562 366"
                                >
                                    <image xlinkHref={process.env.PUBLIC_URL +right} width="600" height="600" opacity="0.5" />
                                </pattern>
                            </React.Fragment>
                        ))}
                    </defs>
                </svg>

                {slides.map(({ id }, i) => (
                    <div
                        key={id}
                        className={`slide ${i === activeIndex ? "active" : ""}`}
                        id={id}
                        aria-hidden={i !== activeIndex}>
                        <div className="slide__images">
                            {["left", "right"].map((side) => (
                                <div key={side} className={`slide__image slide__image--${side}`}>
                                    {side === "left" && (
                                        <div className="slide__text">
                                            <h1>{slides[activeIndex].leftTextTitle}</h1>
                                            <p>{slides[activeIndex].leftTextDesc}</p>
                                        </div>
                                    )}
                                    {side === "right" && (
                                        <div className="right-overlay">
                                            {slides[i].rightContent}
                                        </div>
                                    )}

                                    <svg
                                        viewBox="0 0 900 365"
                                        xmlns="http://www.w3.org/2000/svg"
                                        xmlnsXlink="http://www.w3.org/1999/xlink"
                                        xmlSpace="preserve"
                                        x="0"
                                        y="0"
                                    >
                                        <path
                                            d={
                                                side === "left"
                                                    ? "M 0 0 L 0 365 L 351.2382 365 L 562 0 L 0 0 Z"
                                                    : "M 900 365 L 900 0 L 548.7618 0 L 338 365 L 900 365 Z"
                                            }
                                            fill={`url(#pattern${i + 1}${side === "left" ? "l" : "r"})`}
                                        />
                                    </svg>
                                </div>
                            ))}
                        </div>
                    </div>
                ))}

                <div className="slider__pagination">
                    {slides.map(({ id, btn }, i) => (
                        <button
                            key={id}
                            className={`button ${i === activeIndex ? "active" : ""}`}
                            onClick={() => setActiveIndex(i)}
                            aria-label={`Slide ${i + 1}`}
                        >
                            {btn}
                        </button>
                    ))}
                </div>
            </div>
        </div>
    );
}
