
import './App.scss';

import Profile from './components/Profile';
import PortDetail from './components/PortDetail';

import 'swiper/css';
// Import Swiper styles
import 'swiper/css';
import 'swiper/css/pagination';
import 'swiper/css/navigation';
import { useRef, useState } from 'react';
import Enter2 from './components/Enter2';
import ParticlesBackground from './components/ParticlesBackground';

import { Mousewheel } from 'swiper/modules';
import { Swiper, SwiperSlide } from 'swiper/react';
import Contact from './components/Contact';


function App() {

  const swiperRef = useRef(null);

  const [selectedIndex, setSelectedIndex] = useState(0);
  const handleNavClick = (index) => {
    if (swiperRef.current) {
      swiperRef.current.slideTo(index);
    }
    setSelectedIndex(index); // 선택된 탭 저장!

  };


  return (
    <div>
      <div className="inner_wrap">
        <div className="home_nav">
          <ul>
            <li onClick={() => handleNavClick(0)} className={selectedIndex === 0 ? 'active' : ''}>HOME</li>
            <li onClick={() => handleNavClick(1)} className={selectedIndex === 1 ? 'active' : ''}>PROFILE</li>
            <li onClick={() => handleNavClick(2)} className={selectedIndex === 2 ? 'active' : ''}>PORTFOLIO</li>
            <li onClick={() => handleNavClick(3)} className={selectedIndex === 3 ? 'active' : ''}>CONTACT</li>
          </ul>
        </div>

        <div className="back_ground_wrap">
          <ParticlesBackground />


          <Swiper
            direction={'vertical'}
            mousewheel={true}
            modules={[Mousewheel]}
            nested={true}
            onSwiper={(swiper) => (swiperRef.current = swiper)}
            onSlideChange={(swiper) => {
              setSelectedIndex(swiper.activeIndex);
            }}
            className="mySwiper"
          >
            <SwiperSlide>
              <div className={`fade-in ${selectedIndex === 0 ? 'active' : ''}`}>
                <Enter2 />              
              </div>  
              <div className="sc_down blink">
                  <img src={process.env.PUBLIC_URL +"/img/bottom_scroll.png"} alt="icon"/>
                </div>
            </SwiperSlide>

            <SwiperSlide>
              <div className={`fade-in ${selectedIndex === 1 ? 'active' : ''}`}>
                <Profile />
                <div className="sc_down blink">
                  <img src={process.env.PUBLIC_URL +"/img/bottom_scroll.png"} alt="icon" />
                </div>
              </div>

            </SwiperSlide>

            <SwiperSlide>
              <div className={`fade-in ${selectedIndex === 2 ? 'active' : ''}`}>
                <PortDetail />
                <div className="sc_down blink">
                  <img src={process.env.PUBLIC_URL +"/img/bottom_scroll.png"} alt="icon"/>
                </div>
              </div>

            </SwiperSlide>

            <SwiperSlide>
              <div className={`fade-in ${selectedIndex === 3 ? 'active' : ''}`}>
                <Contact />
              </div>
            </SwiperSlide>
          </Swiper>
        </div>
      </div>


    </div>
  );
}

export default App;
