import React from 'react'
import { useRef, useState } from 'react'

const Enter2 = () => {
    const videoRef = useRef(null);
    const [hovered, setHovered] = useState(false);
    const handleMouseEnter = () => {
        if (!hovered) {
            setHovered(true);
            const video = videoRef.current;

            const playVideo = () => {
                video.play().catch((error) => {
                    console.log('Video play error:', error);
                });
            };

            if (video.readyState >= 3) {
                video.currentTime = video.duration * 0.75;
                playVideo();
            } else {
                video.oncanplay = () => {
                    video.currentTime = video.duration * 0.75;
                    playVideo();
                };
            }
        }
    };

    const handleMouseLeave = () => {
        if (hovered) {
            setHovered(false);
            const video = videoRef.current;
            video.pause();
            video.currentTime = 0;
        }
    };

    const headlineLines = [
        'THANKS FOR',
        'VISTING',
        'MY WEB PUBLISHING',
        'PORTFOLIO',
    ];
    return (
        <div>
            <div className="enter_wrap" >
               
                    <div className="headline"> 
                       
                        {headlineLines.map((line, lineIndex) => (
                            <h1 key={lineIndex} className="headline__text">
                                {line.split('').map((char, charIndex) => (
                                    <span
                                        key={charIndex}
                                        className="headline__char"
                                        style={{ animationDelay: `${(lineIndex * 0.6) + charIndex * 0.05}s` }}
                                    >
                                        {char === ' ' ? '\u00A0' : char}
                                    </span>
                                ))}
                            </h1>
                        ))}
                    </div> 
                    <p style={{textAlign:'right' , fontSize :'14px' ,paddingRight:'50px'}}>이 사이트는 1920 X 1080 에 최적화되었습니다.</p>
                    <div
                        className="hover_area"
                        onMouseEnter={handleMouseEnter}
                        onMouseLeave={handleMouseLeave}
                    >
                        <div className="neon_effect"></div>
                        <video
                            ref={videoRef}
                            src={process.env.PUBLIC_URL +"/img/drawing.mp4"}
                            muted
                            loop
                            className={`enter_video ${hovered ? 'visible' : ''}`}
                        />
                    </div>

                    <div className="hover_area2">
                        <div className="neon_effect"></div>
                        <div className="circle_color">
                        </div>
                    </div>

                    <div className="hover_area3">
                        <div className="neon_effect"></div>
                        <div className="skill_up">
                            <ul>
                                <li><img src={process.env.PUBLIC_URL +"/img/html_icon.png"} alt="icon" />HTML</li>
                                <li><img src={process.env.PUBLIC_URL +"/img/css_icon.png"} alt="icon" />CSS</li>
                                <li><img src={process.env.PUBLIC_URL +"/img/scss_icon.png"} alt="icon" />SCSS</li>
                                <li><img src={process.env.PUBLIC_URL +"/img/java_icon.png"} alt="icon" />JS</li>
                                <li><img src={process.env.PUBLIC_URL +"/img/jq_icon.png"} alt="icon" />JQ</li>
                                <li><img src={process.env.PUBLIC_URL +"/img/react_icon.png"} alt="icon" />React</li>
                                <li><img src={process.env.PUBLIC_URL +"/img/redux_icon.png"} alt="icon" />Rdux</li>
                                <li><img src={process.env.PUBLIC_URL +"/img/vs_icon.png"} alt="icon" />VS</li>
                                <li><img src={process.env.PUBLIC_URL +"/img/photo_icon.png"} alt="icon" />PS</li>
                                <li><img src={process.env.PUBLIC_URL +"/img/illust_icon.png"} alt="icon" />AI</li>
                                <li><img src={process.env.PUBLIC_URL +"/img/figma_icon.png"} alt="icon" />Figma</li>
                                <li><img src={process.env.PUBLIC_URL +"/img/mysql_icon.png"} alt="icon" />MySql</li>
                            </ul>
                        </div>
                    </div>
            </div>
        </div>
    )
}

export default Enter2