import { useCallback } from "react";
import Particles from "react-tsparticles";
import { loadFull } from "tsparticles";

const ParticlesBackground = () => {
  const particlesInit = useCallback(async (engine) => {
    // ✅ 이걸 꼭 await로 loadFull 해줘야 함
    await loadFull(engine);
  }, []);

  const particlesLoaded = useCallback(async (container) => {
    // 콘솔에서 확인용
    console.log(container);
  }, []);

  return (
    <Particles
      id="tsparticles"
      init={particlesInit}
      loaded={particlesLoaded}
      options={{
        fullScreen: { enable: true, zIndex: 0 },
        background: {
          color: {
            value: "#070825",
          },
        },
        particles: {
          number: {
            value: 355,
            density: {
              enable: true,
              area: 789.15,
            },
          },
          color: {
            value: "#ffffff",
          },
          shape: {
            type: "circle",
          },
          opacity: {
            value: 0.49,
            anim: {
              enable: true,
              speed: 0.25,
              opacity_min: 0,
            },
          },
          size: {
            value: 2,
            random: true,
            anim: {
              enable: true,
              speed: 0.333,
              size_min: 0,
            },
          },
          move: {
            enable: true,
            speed: 0.1,
            random: true,
            out_mode: "out",
          },
        },
        interactivity: {
          events: {
            onhover: {
              enable: true,
              mode: "bubble",
            },
            onclick: {
              enable: true,
              mode: "push",
            },
          },
          modes: {
            bubble: {
              distance: 83.9,
              size: 1,
              duration: 3,
              opacity: 1,
              speed: 3,
            },
          },
        },
        detectRetina: true,
      }}
    />
  );
};

export default ParticlesBackground;
